@import '../../styles/smart-grid';

.product {
    width: 800px;
    max-width: 90%;
    margin: 50px auto;
    text-align: center;

    &-img {
        border-radius: 10px;
        overflow: hidden;
    }

    &-text {
        max-width: 100%;
        margin-top: 30px;
        line-height: 32px;
        font-size: 21px;
    }

    &-tag {
        line-height: 32px;
        font-size: 21px;
    }
}