@import '../../styles/smart-grid';

.about {
    padding: 80px 0;
    p {
        text-align: justify;
        max-width: 90%;
        margin: 30px auto;
        line-height: 32px;
        font-size: 21px;
    }

    &-img {
        max-width: 90%;
        width: 500px;
        margin-bottom: 30px;
    }

    .row {
        justify-content: space-around;
    }

    .declarations img {
        max-width: 250px;
        margin: 10px 10px;
        display: block;
    }
}