@import '../../styles/smart-grid';

.contacts {
    padding: 80px 0;

    .title-primary {
        margin-bottom: 30px;
    }

    .row {
        justify-content: space-evenly;
    }

    .row-inline {
        display: flex;
    }

    .item {
        display: block;
        margin: 10px 0;
    }

    a {
        font-size: 18px;
        color: #333;
        text-decoration: underline;
        padding-left: 20px;
    }

    p {
        font-size: 18px;
        color: #333;
        margin: 10px 0;
    }

    .address, .map {
        @include col();
        @include size(12);
        @include size-sm(20);
    }
}