@import '../../styles/smart-grid';

$color-primary: #0ac20a;

.advantages {
    .row {
        justify-content: space-evenly;
    }
    &-item {
        @include col();
        @include size(8);
        @include size-sm(24);
        @include sm(margin-bottom, 40px);
        
        max-width: 300px;
        text-align: center;
    }

    &-icon {
        width: 80px;
        height: 80px;
        background: $color-primary;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 30px;
        border-radius: 50%;
        margin: 0 auto;
        color: #fff;
    }

    &-title {
        font-size: 30px;
        line-height: 1;
        margin: 20px 0;
    }

    &-text {
        font-size: 18px;
        line-height: 1.5;
    }

    padding: 80px 0;
}