@import '../../styles/smart-grid';

$color-primary: #0ac20a;

.catalog {
    .row {
        justify-content: space-evenly;
        margin: 50px 0;
    }

    &-item {
        @include col();
        @include size(6);
        @include size-sm(12);
        @include size-xs(24);
        max-width: 300px;
        
        box-sizing: border-box;
        padding: 20px 10px;

        text-align: center;
        box-shadow: 0 0 30px rgba($color: #000000, $alpha: .2);
        border-radius: 10px;
        margin-bottom: 40px;

        display: flex;
        flex-direction: column;
        align-items: center;
        transition: box-shadow .3s ease-in-out;
        
        &:hover {
            box-shadow: 0 0 60px rgba($color: #000000, $alpha: .4);
        }
    }

    &-img {
        border-radius: 5px;
        margin-bottom: auto;
    }

    &-title {
        font-size: 22px;
        margin: 10px;
    }

    &-desc {
        font-weight: bold;
        margin-bottom: 10px;
    }

    &-link {
        background-color: $color-primary;

        &:hover {
            background-color: rgba($color: $color-primary, $alpha: .8)
        }
    }
}