@import '../../styles/smart-grid';

$color-primary: #0ac20a;

.contact-form {
    background-color: $color-primary;
    padding: 160px 0;

    @include sm(padding, 60px 0);

    .title-primary {
        font-size: 45px;
        color: #fff;
        margin-bottom: 30px;
    }

    form {
        max-width: 100%;
        width: 500px;
        margin: 0 auto;
    }

    .form-input {
        width: 100%;
        height: 60px;
        border: 1px solid #c6c6c6;
        border-radius: 3px;
        font-size: 1.4rem;
        color: #3b3b3b;
        padding: 0 2.2rem;
        margin-bottom: 3rem;
        box-sizing: border-box;
    }

    .btn-primary {
        width: 80%;
        padding: 10px 0;
        font-size: 23px;
    }
}