@import '../../styles/smart-grid';
@import '../../styles/navbar';

$color-primary: #0ac20a;

header {
    @include navbar('auto', 1170, #0ac20a);

    .navbar {
        padding: 10px 0;
        .logo {
            width: 150px;
        }
        .menu-link {
            font-size: 18px;
            
        }
    }

    @include sm-block {
        .navbar {
            .logo {
                max-width: 100px;
            }
        }
    }
}